.toolsWrap{margin:10px;background:#fff}.toolsWrap .title{padding:5px 0 0 10px;line-height:32px;font-size:16px}.toolsWrap .content{overflow:hidden;margin:0 5px;padding-bottom:5px}.toolsWrap .content .item{float:left;position:relative;width:33.33%;padding:5px}.toolsWrap .content .item.disable{opacity:.75;cursor:default}.toolsWrap .content .item.disable .con:hover{background:#eff4f6!important}.toolsWrap .content .item .con{height:90px;padding:15px;background:#eff4f6;overflow:hidden}.toolsWrap .content .item .con:hover{background:#e6ebed}.toolsWrap .content .item .con .icon{float:left;width:40px;height:40px;margin-top:10px;background:url(data:image/png;base64,)}.toolsWrap .content .item .con .icon.iconSql{background-position:0 0}.toolsWrap .content .item .con .icon.iconDatahub{background-position:0 -40px}.toolsWrap .content .item .con .icon.iconId{background-position:0 -80px}.toolsWrap .content .item .con .icon.iconTask{background-position:0 -120px}.toolsWrap .content .item .con .info{height:60px;margin-left:60px;padding-left:20px;border-left:1px solid #cddade}.toolsWrap .content .item .con .info .tit{font-weight:700;font-size:14px;color:#666}.toolsWrap .content .item .con .info .des{margin-top:5px;color:#757b87;font-size:12px}