header{position:absolute;z-index:1;height:60px;line-height:60px;background:#fafafa;width:100%;border-bottom:1px solid #ebebeb;-webkit-box-shadow:0 1px 2px 0 rgba(0,0,0,.06);box-shadow:0 1px 2px 0 rgba(0,0,0,.06)}header .otherOperate{height:100%}header .otherOperate a{color:#383e49}header .otherOperate span{display:inline-block;padding:0 30px;list-style:none;height:22px;line-height:22px;color:#383e49;cursor:pointer}header .otherOperate span:not(:first-child){border-left:1px solid #ccd1dd}header .otherOperate span i{display:inline-block;vertical-align:text-bottom;border-radius:100%;height:16px;width:16px;text-align:center;border:1px solid #000;line-height:16px;font-size:12px;margin-right:5px}header .otherOperate span label{font-size:12px}